.projectTile {
  display: flex;
  padding: 10px;
  background-image: var(--bg-linear-gradient);
  background-color: var(--main-background-color-alpha);
  color: var(--main-text-color);
  border-radius: 5px;
}

.projectDetails {
  flex-grow: 1;

  .projectName {
    font-style: bold;
    font-size: 20px;
  }
  .projectURL {
    font-size: 16px;
  }
}

.projectControls {
  display: flex;
  align-items: center;
}
