@import "../../../../scss/sass_vars";

.projectPageWrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.projectPage {
  position: relative;
  display: flex;
  justify-content: space-around;
  gap: 20px;

  @media screen and (max-width: $mobileBreakpoint) {
    flex-direction: column;
  }
}

.projectHeader {
  display: flex;
  justify-content: space-between;
}

.settings {
  width: 40%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  @media screen and (max-width: $mobileBreakpoint) {
    width: 100%;
    flex-direction: column;
  }
}

.knowledgeBase {
  width: 60%;

  .knowledgeBaseList {
    max-height: 200px;
    overflow-y: auto;
  }

  .knowledgeBaseUploadForm {
    position: relative;
    height: 200px;
    padding: 5px;
    border: 5px dashed var(--color-border-main);
    border-radius: 10px;
    overflow-x: hidden;
    overflow-y: auto;

    input {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      background-color: #fff;
    }
  }

  summary {
    margin-bottom: 15px;
  }

  @media screen and (max-width: $mobileBreakpoint) {
    width: 100%;
  }
}

.projectPageSection {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  background-color: var(--main-background-color-alpha);

  .sectionHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding: 7px;
    border-radius: 5px;
    background-image: var(--bg-linear-gradient);
    background-color: var(--main-background-color-alpha);
  }

  .sectionContent {
    padding: 10px;
  }
}

.scriptCode {
  width: 100%;
  display: block;
  overflow-x: auto;
  white-space: pre;
}

.buttonsWrapper {
  display: flex;
  gap: 10px;
}

.dragndropMessage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

.filesList {
  list-style: none;

  li {
    display: flex;
    justify-content: space-between;
  }
}
